html,
body {
  height: 100%;
}

body {
  font-family: 'Raleway', sans-serif;
  overflow-x: hidden;
}

input,
button,
textarea,
a {
  outline: none !important;
}

h1{
  font-size: 40px;
  color:#fff;
  text-decoration: underline;
  display: block;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  margin-top: 20%;
}

a,
a:hover {
  text-decoration: none;
}

ul,
li {
  display: block;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  color: #fff;
}

img {
  max-width: 100%;
}

.header {
  background: url(../img/header-bg.jpeg) no-repeat center top / cover;
  padding-bottom: 170px;
  min-height: 100vh;
}

.head {
  background: rgba(0, 0, 0, 0.35);
}

.pizza__slide {
  outline: none;
  border-top: 1px solid #684a37;
  border-bottom: 1px solid #684a37;
  padding: 20px 100px;
  display: flex;
  align-items: center;
}

.logo__text {
  font-size: 25px;
  font-family: 'Nautilus', sans-serif;
  transform: matrix(1, -0.13485275891962, 0, 1, 0, 0);
  line-height: 25px;
  float: left;
  margin-left: 11px;
}

.logo__text_color {
  color: #c59d5f;
}

.logo__img {
  float: left;
}

.head {
  padding-top: 33px;
  padding-bottom: 33px;
}

.head__menu_item {
  float: left;
  margin-right: 43px;
  margin-top: 10px;

}

.head__menu_item a {
  color: #fff;
  border-bottom: 1px solid #67635e;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  transition: 0.2s;
}

.head__menu_item i {
  color: #67635e;
  font-size: 13px;
  margin-right: 5px;
  padding: 3px 5px;
  transition: 0.2s;
}

.head__menu_item:hover a {
  color: #c59d5f;
  border: none;
}

.head__menu_item:hover i {
  background: #c59d5f;
  border-radius: 50%;
}

.head__menu_item:last-child {
  margin-right: 0;
}

.contacts {
  float: right;
}

.contacts__desc {
  font-size: 12px;
  color: #67635e;
  float: right;
  line-height: 3px;
  margin-top: 11px;
  letter-spacing: 1px;
}

.contacts__telephone {
  font-size: 24px;
  font-family: Arial, sans-serif;
  float: right;
}

.contacts__lower {
  font-size: 16px;
}

.contacts__telephone i {
  color: #c59d5f;
  transform: rotate(90deg);
}

.pizza__text {
  float: left;
  width: 360px;
}

.pizza__img {
  width: 550px;
  float: right;
}

.pizza__name {
  font-family: Isadora, sans-serif;
  color: #fff;
  font-size: 52px;
}

.pizza__desc {
  font-size: 16px;
  color: #e0e0e0;
  width: 300px;
  margin-top: 25px;
}

.pizza__cost {
  font-size: 14px;
  color: #e0e0e0;
  margin-top: 25px;
}

.choose-btn {
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  background: #c59d5f;
  padding: 9px 30px;
  margin-top: 56px;
  display: inline-block;
  border: 3px solid transparent;
  transition: 0.2s;
  outline: none;
  cursor: pointer;
}

.choose-btn i {
  font-size: 12px;
  margin-right: 4px;
  margin-left: -4px;
}

.choose-btn:hover {
  border: 3px solid #fff;
  box-shadow: 0 0 25px 5px #c59d5f;
  color: #fff;
}

.choose-btn:active {
  color: #c59d5f;
  background: #ffffff;
  border-color: #c59d5f;
  box-shadow: inset 0 0 25px 5px #c59d5f;
}

.slick-arrow {
  color: #c5c3c3;
  font-size: 28px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  transition: 0.2s;
}

.fa-chevron-left.slick-arrow {
  left: -30px;
}

.fa-chevron-right.slick-arrow {
  right: -30px;
}

.slick-arrow:hover {
  background: rgba(197, 157, 95, 0.2);
}

.basket-btn {
  position: fixed;
  right: 30px;
  top: 30px;
  font-size: 25px;
  color: #e0e0e0;
  border: none;
  background: none;
  cursor: pointer;
  transition: .3s;
  z-index: 5;
}

.basket-btn:hover {
  color: #e58e3d;
  transform: scale(1.3);
}

.basket-toggle {
  display: none;
}

.pizza-out {
  max-height: calc(100vh - 217px);
  min-height: 100px;
  overflow-y: scroll;
}

.basket-out {
  width: 400px;
  position: fixed;
  right: -400px;
  top: 30px;
  background: #ffffff;
  color: #000;
  border: 2px solid #fca448;
  transition: .3s;
  z-index: 5;
  padding: 20px;
}

.pizza-out::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

.pizza-out::-webkit-scrollbar-track {
  background: #fff;
}

.pizza-out::-webkit-scrollbar-thumb {
  background: #684a37;
}

.basket-toggle:checked ~ .basket-btn {
  color: #fcb362;
  right: 430px;
}

.basket-toggle:checked ~ .basket-btn:hover {
  color: #c12f06;
}

.basket-toggle:checked ~ .basket-out {
  right: 0;
}

.basket__pizza-plus,
.basket__pizza-minus {
  display: inline-block;
  color: #a50000;
  cursor: pointer;
  transition: .3s;
  margin-left: 20px;
}

.basket__pizza-plus {
  margin-left: 10px;
}

.basket__pizza-plus:hover,
.basket__pizza-minus:hover {
  transform: scale(1.5);
}

.order-form {
  margin-top: 15px;
}

.input-email {
  width: 100%;
  border: 1px solid #e58e3d;
  border-radius: 5px;
  padding: 5px 3px;
  margin-bottom: 10px;
  background: none;
}

.input-name,
.input-tel {
  @extend .input-email;
  width: 48%;
}

.input-name {
  float: left;
}

.input-tel {
  float: right;
}

.sbm-btn {
  background-color: #dd2d07;
  width: 48%;
  float: right;
  border: 3px solid #3a3a3a;
  color: #fff;
  border-radius: 5px;
  font-weight: 700;
  cursor: pointer;
  transition: .3s;
  padding: 5px 0;
}

.sbm-btn:hover {
  box-shadow: 0 0 25px 5px #dd2d07;
  background: #3a3a3a;
  border: 3px solid #dd2d07;
}

.sbm-btn:active {
  background: #3a3a3a;
  color: #fff;
  box-shadow: inset 0 0 25px 5px #dd2d07;
  border: 3px solid #3a3a3a;
}

.basket__pizza-wrap {
  border-top: 2px solid #684a37;
  border-bottom: 2px solid #684a37;
  padding: 5.5px 0;
  position: relative;
}

.cost-of-all {
  color: #000;
  font-size: 20px;
  float: left;
  font-weight: 700;
  margin-top: 4px;
  width: 50%;
}

.basket__pizza-txt {
  float: left;
  width: 50%;
}

.basket__pizza-name {
  font-size: 30px;
  width: 100%;
  margin-bottom: 49px;
}

.basket__pizza-cost,
.basket__pizza-count {
  color: #000;
  width: 100%;
  font-size: 20px;
  margin-top: 5px;
}

.basket__pizza-lower {
  font-size: 14px;
}

.basket__pizza-img {
  float: right;
  width: 50%;
}

.basket__pizza-del {
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 16px;
  color: red;
  cursor: pointer;
  transition: .3s;
}

.basket__pizza-del:hover {
  transform: scale(1.5);
}

.learn-more-btn{
  background: none;
  color:#fff;
  cursor: pointer;
  border:none;
  text-decoration: underline;
}